@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.journal-header {
    background-color: #F55A5A;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.journal-header--title {
    padding: 0 5px;
}
.card {
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-style: normal;
    font-weight: normal;
}

.card--image {
    padding: 20px;
    height: 180px;
    width: 180px;
    border-radius: 30px;
}

.card--info {
    padding: 20px;
    width: 400px;
    min-width: 400px;
}

.card--info--gps {
    width: 7px;
}

.card--info--title,
.card--info--date,
.card--info--description {
    margin: 0;
}

.card--info--country {
    margin-left: 5px;
    margin-right: 15px;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.17em;
    color: #2B283A;
}

.card--info-gmaps {
    
    font-size: 12px;
    line-height: 12px;
    color: #918E9B;
}

.card--info--title {
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    color: #2B283A;
    padding: 7px 0;
}

.card--info--dates {
    font-weight: bold;
    font-size: 12px;
    line-height: 12px;
    color: #2B283A;
}

.card--info--description {
    font-size: 12px;
    line-height: 150%;
    color: #2B283A;
}

.card-separator {
    width: 600px;
    border: 1px solid #F5F5F5;
}
